.popup {
    background: #ffffff;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    position: relative;
    display: grid;
    flex-direction: column;
    max-width: 430px;
    min-width: 282px;
    width: 100%;
    grid-template-columns: minmax(36px, 1fr) 10fr minmax(36px, 1fr);
    grid-auto-rows: max-content;
    grid-template-areas: ". formTitle ." ". txtName ." ". txtError1 ." ". txtTitle ." ". txtError2 ." ". btnSubmit .";
    padding-bottom: 37px;
}

@media screen and (max-width: 600px) {
    .popup {
        grid-template-columns: minmax(22px, 1fr) 10fr minmax(22px, 1fr);
    }
}

@media screen and (max-width: 320px) {
    .popup {
        grid-template-columns: minmax(10px, 22px) 10fr minmax(10px, 22px);
        padding-bottom: 25px;
    }
}