.header {
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
@media screen and (max-width: 918px) {
  .header {
    padding: 0 19px;
  }
}
@media screen and (max-width: 320px) {
  .header {
    padding-left: 27px;
  }
}